.bgColor{
    background-color: #0A192F;
}
.navbar-nav .nav-item{ 
    position: relative;
}
.navbar-nav .nav-item::after{
    content: '';
	width: 0%;
	height: 3px;
	display: block;
    background-color: #fff;
    margin: auto;
    border-radius: 5px;
    transition: .3s;
}
.navbar-nav .nav-item:hover::after{
    width: 90%;
}
.stickyNav{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background-image: linear-gradient(#9b73ea, #7532F9);
    box-shadow: 1px 1px 8px #222;
}
.header{
    min-height: 95vh;
    letter-spacing: 1px;
}
.name{
    font-size: 55px;
}
.color{
    color: #9b73ea;
}
.h-img{
    height: 440px;
    width: 100%;
    object-fit: cover;
    border-radius: 50%;
    background-image: linear-gradient(#9b73ea, #7532F9);
}

@media (max-width:500px) {
    .h-img{
        height: 400px;
    }
    .name{
        font-size: 40px;
    }
    .navbar-nav .nav-item:hover::after{
        width: 30%;
    }
}
@media (max-width:370px) {
    .h-img{
        height: 300px;
    }
}

.button{
    background-image: linear-gradient(#7532F9, #9b73ea);
    color: #fff;
    padding: 10px 12px;
    text-decoration: none;
    display: inline-block;
    letter-spacing: 1px;
}
.button:active{
    background-image: linear-gradient(#7532F9, #9b73ea) !important;
}
.button:hover{
    background-image: linear-gradient(#9b73ea, #7532F9);
}
/* skills section */
.progress-bg{
    background-color: #7532F9;
}

/* projects section */
.field{
    border: 2px solid gray;
}
textarea.field{
    resize: none;
}
.scrolltotop{
    position: fixed;
    bottom: -15%;
    right: 4%;
    cursor: pointer;
    transition: .3s;
}
.scrolltotop i{
    color: #7532F9;
    font-size: 50px;
}
.show{
    bottom: 5%;
}
@media (max-width:500px) {
    .show{
        bottom: 2%;
    }
}
